<template>
  <view>
    <swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000" class="swiper" circular="true">
      <swiper-item v-for="list in swiperList" :key="list.id" class="swiper-item">
        <img :src="list.blurPicUrl" alt="">
      </swiper-item>
    </swiper>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        swiperList:[]
      };
    },
    methods:{
      //获取轮播图数据
      async getSwiperList(){
        let result = await uni.$http.get('http://localhost:3000/album/newest')
        if(result.data.code !== 200)uni.$showMsg()
        this.swiperList = result.data.albums
        console.log(this.swiperList);
      }
    },
    onLoad() {
      this.getSwiperList()
    },
    onShow() {

    }
  }
</script>

<style lang="less">
.swiper{
  width: 90%;
  margin: auto;
  .swiper-item{
    img{
      width: 100%;
      height: 100%;
      object-fit:none;
    }
  }
}
</style>
